  height: 77px;
  background-color: #f7f7f7;
  justify-content: center;
  align-items: center;
  gap: 10px;
  margin-left: calc(-50vw - 50%);
  margin-right: calc(-50vw - 50%);
  font-size: 16px;
  font-weight: 400;
  line-height: 1.16;
  display: flex;
  position: relative;
  left: 100%;
}

.information_icon {
  width: 14px;
  height: 12.88px;
}

